/*========Typewriter effect==========*/
/* body{
  padding: 40px;
  background-color: #121212;  
} */

.t-p {
  border-right: solid 3px var(--main-white-color);
  white-space: nowrap;
  overflow: hidden;    
  /* font-family: 'Source Code Pro', monospace;   */
  /* font-size: 28px; */
  font-size: 50px;
  color: var(--main-white-color);
  margin: 0 auto;
  text-align: center
}

/* Animation */
.t-p {
  animation: animated-text 3s steps(29,end) 1s 1 normal both,
             animated-cursor 600ms steps(29,end) infinite;
}

/* text animation */

@keyframes animated-text{
  from{width: 0;}
  to{width: 570px;}
}

/* cursor animations */

@keyframes animated-cursor{
  from{border-right-color: var(--main-white-color)}
  to{border-right-color: transparent;}
}